@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond:ital,wght@0,500;0,700;1,500;1,700&family=Lato:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: lato;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0c0c0c;
  color: whitesmoke;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a,abbr,acronym,address,applet,article,aside,audio,b,big,blockquote,body,canvas,caption,center,cite,code,dd,del,details,dfn,div,dl,dt,em,embed,fieldset,figcaption,figure,footer,form,h1,h2,h3,h4,h5,h6,header,hgroup,html,i,iframe,img,ins,kbd,label,legend,li,mark,menu,nav,object,ol,output,p,pre,q,ruby,s,samp,section,small,span,strike,strong,sub,summary,sup,table,tbody,td,tfoot,th,thead,time,tr,tt,u,ul,var,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:after,blockquote:before,q:after,q:before{content:'';content:none}table{border-collapse:collapse;border-spacing:0}input[type=search]::-webkit-search-cancel-button,input[type=search]::-webkit-search-decoration,input[type=search]::-webkit-search-results-button,input[type=search]::-webkit-search-results-decoration{-webkit-appearance:none;-moz-appearance:none}input[type=search]{-webkit-appearance:none;-moz-appearance:none;box-sizing:content-box}textarea{overflow:auto;vertical-align:top;resize:vertical}audio,canvas,video{display:inline-block;max-width:100%}audio:not([controls]){display:none;height:0}[hidden]{display:none}html{font-size:100%;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%}a:focus{outline:thin dotted}a:active,a:hover{outline:0}img{border:0;-ms-interpolation-mode:bicubic}figure{margin:0}form{margin:0}fieldset{border:1px solid silver;margin:0 2px;padding:.35em .625em .75em}legend{border:0;padding:0;white-space:normal}button,input,select,textarea{font-size:100%;margin:0;vertical-align:baseline}button,input{line-height:normal}button,select{text-transform:none}button,html input[type=button],input[type=reset],input[type=submit]{-webkit-appearance:button;cursor:pointer}button[disabled],html input[disabled]{cursor:default}input[type=checkbox],input[type=radio]{box-sizing:border-box;padding:0}input[type=search]{-webkit-appearance:textfield;box-sizing:content-box}input[type=search]::-webkit-search-cancel-button,input[type=search]::-webkit-search-decoration{-webkit-appearance:none}button::-moz-focus-inner,input::-moz-focus-inner{border:0;padding:0}textarea{overflow:auto;vertical-align:top}table{border-collapse:collapse;border-spacing:0}button,html,input,select,textarea{color:#222}::selection{background:#b3d4fc;text-shadow:none}img{vertical-align:middle}fieldset{border:0;margin:0;padding:0}textarea{resize:vertical}.chromeframe{margin:.2em 0;background:#ccc;color:#000;padding:.2em 0}

.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

* { box-sizing: border-box; }

.visuallyHidden:not(:focus):not(:active) { position: absolute; width: 1px; height: 1px; margin: -1px;border: 0;padding: 0;white-space: nowrap;-webkit-clip-path: inset(100%);clip-path: inset(100%);clip: rect(00 0 0);overflow: hidden;}
.wrapper {
  max-width: 1280px;
  margin: 0 auto;
}

footer {
  text-align: center;
  padding: 20px 0;
}


.listingsContainer {
    padding: 60px 0;
}

ul {
    display: flex;
}
li {
    list-style: none;
    width: calc(50% - 40px);
    margin: 20px;
}
img {
    width: calc(50% - 40px);
    margin: 20px;
}
p {
    font-family: lato;
    font-size:clamp(1rem, 2vw, 2rem);
    font-weight: 300;
    padding: 5px 0;
}
p.address {
    font-family: 'Cormorant Garamond';
    font-size: clamp(2rem, 3vw, 4rem);
    padding: 10px 0;
}

@media (max-width: 768px) {
    ul {
        display: block;
    }
    img, li {
        width: calc(100% - 40px);

    }
}

header {
  height: 80vh;
  font-family: lato;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: whitesmoke;
}

.headerContainer {
  display: flex;
  justify-content: space-between;
  
}

h1 {
  font-size: clamp(3rem, 5vw, 8rem);
}


.fancy {
  font-family: 'Cormorant Garamond';
  font-weight: 600;
  font-style: italic;
}

.realEstate {
  text-transform: uppercase;
  font-weight: 400;
}
.thin {
  font-weight: 300;
}


.scrollButton {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  height: 100%;
}

button {
  width: 50%;
  background-color: #0C0C0C; 
  border: 1px solid whitesmoke;
  color: whitesmoke;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  transition: 0.5s ease-in-out all;
}

button:hover, button:focus {
    color: #0C0C0C;
    background-color: whitesmoke;
}

.buttonsContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.buttonsContainer button {
  margin: 10px;
}

@media (max-width: 768px) {
    header {
        align-items: center;
    }
}
